import React, { useCallback, useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useParams } from "react-router-dom";
import { Button } from "primereact/button";
import { Card } from "primereact/card";
import { Dropdown } from "primereact/dropdown";
import { InputText } from "primereact/inputtext";
import { InputTextarea } from "primereact/inputtextarea";
import { Message } from "primereact/message";
import { shallowEqual } from "react-redux";
import { useAppDispatch, useAppSelector } from "../../utilities/hooks";
import { glAccountActions } from "../../store/glAccount/glAccountSlice";
import { vendorActions } from "../../store/vendor/vendorSlice";
import { userActions } from "../../store/user/userSlice";
import { IAppState } from "../../models/appState";
import { IVendor } from "../../models/vendor";
import { v4 as uuidv4 } from "uuid";

const VendorEdit: React.FC = () => {
  const defaultItem: IVendor = {
    id: "",
    name: "",
    phone: "",
    email: "",
    website: "",
    glAccountId: "",
    category: "",
    street: "",
    city: "",
    state: "",
    pobox: "",
    postalcode: "",
    country: "",
    description: "",
    assignedUserId: "",
    assignedUserLastName: "",
  };
  const navigateTo = "/vendors";
  const navigate = useNavigate();
  const [subtitle, setSubtitle] = useState("Create");
  const [item, setItem] = useState(defaultItem);
  const [dirty, setDirty] = useState(false); // if form input has been changed, dirty is true
  const [valid, setValid] = useState(false); // if form input is valid, valid is true
  const [errors, setErrors] = useState({}); // indicates if fields are valid
  const params = useParams();
  const memo = useRef(true);

  const [vendor, isFetching, isError, glAccounts, users, vendors] =
    useAppSelector(
      (state: IAppState) => [
        state.vendors.vendor,
        state.vendors.isFetching,
        state.vendors.isError,
        state.glAccounts.glAccounts,
        state.users.users,
        state.vendors.vendors,
      ],
      shallowEqual
    );
  const dispatch = useAppDispatch();

  useEffect(() => {
    if (memo.current) {
      memo.current = false;
      dispatch(glAccountActions.getGLAccounts());
      dispatch(userActions.getUsers());
      if (params.mode === "edit") {
        setSubtitle("Edit");
        const id = params.id;
        if (id) {
          dispatch(vendorActions.getVendorById(id));
        }
      }
    }
  }, [params, dispatch]);

  useEffect(() => {
    if (
      params.mode === "edit" &&
      item.id !== vendor?.id &&
      !isFetching &&
      !isError
    ) {
      setItem(vendor);
    }
  }, [params, item, vendor, isFetching, isError]);

  const isNameValid = useCallback(
    (name: string) => {
      let result = true;
      if (item.id.length === 0) {
        if (
          vendors.find((x) => x.name?.toLowerCase() === name?.toLowerCase())
        ) {
          result = false;
        }
      } else {
        if (
          vendors.find(
            (x) =>
              x.name?.toLowerCase() === name?.toLowerCase() && x.id !== item.id
          )
        ) {
          result = false;
        }
      }

      return result;
    },
    [item.id, vendors]
  );

  useEffect(() => {
    if (!dirty) {
      return;
    }

    setValid(
      (item.name || "").trim().length > 0 && isNameValid(item.name || "")
    );
  }, [dirty, item, isNameValid]);

  const onChange = (e: any) => {
    setDirty(true);
    const {
      target: { name: tname, value: tvalue },
    } = e;

    if (tname === "assignedUserId") {
      const user = users.find((value) => value.id === tvalue);
      if (user) {
        setItem({
          ...item,
          [tname]: tvalue,
          assignedUserLastName: user.lastName,
        });
      }
    } else {
      setItem({ ...item, [tname]: tvalue });
    }

    if (tname === "name") {
      const isValidName = isNameValid(tvalue);
      setErrors({
        ...errors,
        nameInvalid: !isValidName,
        [tname]: tvalue ? tvalue.toString().trim().length === 0 : true,
      });
    }
  };

  const onSaveClick = async () => {
    if (subtitle !== "Edit") {
      item.id = uuidv4();
    }

    dispatch(vendorActions.updateVendor(item));
    setTimeout(() => {
      dispatch(vendorActions.getVendors());
    }, 300);
    navigate(navigateTo);
  };

  const onCancelClick = async () => {
    navigate(navigateTo);
  };

  return (
    <>
      <h3 className="caption">Vendor: {subtitle}</h3>
      <div className="grid">
        <div className="col-12">
          <span className="field-required">*</span> - Required field
        </div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Vendor</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="VendorName">
                  Vendor Name <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <InputText
                  id="VendorName"
                  name="name"
                  maxLength={150}
                  placeholder="Vendor Name"
                  value={item.name}
                  onChange={onChange}
                />
                {(errors as any)?.name && (
                  <Message severity="error" text="Vendor Name is required." />
                )}
                {(errors as any)?.nameInvalid && (
                  <Message
                    severity="error"
                    text="Vendor Name already exists."
                  />
                )}
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="VendorEmail">Vendor Email</label>
              </div>
              <div className="col">
                <InputText
                  id="VendorEmail"
                  name="email"
                  maxLength={255}
                  placeholder="Example jdoe@example.com"
                  value={item.email}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="VendorPhone">Vendor Phone</label>
              </div>
              <div className="col">
                <InputText
                  id="VendorPhone"
                  name="phone"
                  maxLength={25}
                  placeholder="234-567-8901"
                  value={item.phone}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="GLAccount">GL Account</label>
              </div>
              <div className="col">
                <Dropdown
                  id="GLAccount"
                  name="glAccountId"
                  options={glAccounts}
                  optionLabel="name"
                  optionValue="id"
                  value={item.glAccountId}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Website">Website</label>
              </div>
              <div className="col">
                <InputText
                  id="Website"
                  name="website"
                  maxLength={255}
                  value={item.website}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="Category">Category</label>
              </div>
              <div className="col">
                <InputText
                  id="Category"
                  name="category"
                  maxLength={255}
                  value={item.category}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="AssignedTo">Assigned To</label>
              </div>
              <div className="col">
                <Dropdown
                  id="AssignedTo"
                  name="assignedUserId"
                  options={users}
                  optionLabel="lastName"
                  optionValue="id"
                  value={item.assignedUserId}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col hidden-xs">&nbsp;</div>
              <div className="col hidden-xs">
                <div className="p-fixed-switch">&nbsp;</div>
              </div>
            </div>
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Address</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Street">Street</label>
              </div>
              <div className="col">
                <InputText
                  id="Street"
                  name="street"
                  maxLength={150}
                  value={item.street}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="POBox">PO Box</label>
              </div>
              <div className="col">
                <InputText
                  id="POBox"
                  name="pobox"
                  maxLength={150}
                  value={item.pobox}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="City">City</label>
              </div>
              <div className="col">
                <InputText
                  id="City"
                  name="city"
                  maxLength={100}
                  value={item.city}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="State">State</label>
              </div>
              <div className="col">
                <InputText
                  id="State"
                  name="state"
                  maxLength={100}
                  value={item.state}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col">
            <div className="grid">
              <div className="col">
                <label htmlFor="PostalCode">Postal Code</label>
              </div>
              <div className="col">
                <InputText
                  id="PostalCode"
                  name="postalcode"
                  maxLength={20}
                  value={item.postalcode}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="Country">Country</label>
              </div>
              <div className="col">
                <InputText
                  id="Country"
                  name="country"
                  maxLength={255}
                  value={item.country}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Description</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <InputTextarea
              name="description"
              value={item.description}
              onChange={onChange}
              autoResize
            />
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
        <div className="col-12">
          <Button
            icon="pi pi-check"
            className="p-button-rounded p-button-outlined"
            tooltip="Save"
            disabled={!valid}
            onClick={onSaveClick}
          />
          &nbsp; &nbsp;
          <Button
            icon="pi pi-times"
            className="p-button-rounded p-button-outlined"
            tooltip="Back to List"
            onClick={onCancelClick}
          />
        </div>
      </div>
    </>
  );
};

export default VendorEdit;
